@media  screen and (max-width: 767px) {
  .form__field input {
    //max-width: 230px;
    width: 100%;
  }
}


.form__field input:hover {
  opacity: 0.8;
}

.form__field input:active {
  opacity: 0.5;
}


.form__field-check {
  position: relative;
  display: flex;
  min-height: 40px;
}

.form__field-check input {

  width: 18px;
  height: 18px;
  margin-bottom: 0;
}

.form__field-check label {
  padding-top: 4px;
  padding-left: 23px;
}

.form__field label {
  font-weight: normal;
  font-size: 10px;
  line-height: 13px;
  color: #F9FBFD;
}

.form__checkbox + label::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;

  width: 18px;
  height: 18px;

  background-color: #F9FBFD;
}

.form__checkbox:checked + label::after {
  content: "";

  position: absolute;
  top: 5px;
  left: 3px;

  width: 12px;
  height: 8px;

  background-image: url("../img/check.png");
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: 12px 8px;
  //background-color: red;
}

.form__checkbox:checked + label::before {
  background-image: none;
}

.form__checkbox:disabled + label {
  opacity: 0.5;
}

.form__button {
  margin: 0;
  padding: 0;
  margin-top: 8px;
  padding-top: 12px;
  padding-bottom: 10px;
  padding-left: 90px;
  padding-right: 89.5px;

  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  color: #011C40;

  background-color: #F9FBFD;
  border: none;
  cursor: pointer;
}



.form__button:hover {
  opacity: 0.8;
}

.form__button:active {
  opacity: 0.5;
}

@media  screen and (max-width: 767px) {
  .form__button {
    //max-width: 230px;
    width: 100%;
    padding-left: 53px;
    padding-right: 53px;
  }
}

