.contacts {
  background-color: #E5E5E5;
}

.contacts__wrapper {
  display: flex;
  justify-content: space-between;
  padding-top: 120px;
  padding-bottom: 113px;
  padding-left: 103px;
  padding-right: 103px;
}

@media  screen and (max-width: 1023px) {
  .contacts__wrapper {
    padding-top: 100px;
    padding-bottom: 93px;
    padding-left: 45px;
    padding-right: 45px;
  }
}

@media  screen and (max-width: 767px) {
  .contacts__wrapper {
    //max-width: 295px;
    //justify-content: center;
    flex-direction: column;
    //align-items: center;

    padding-top: 65px;
    padding-bottom: 65px;
    padding-left: 15px;
    padding-right: 15px;
  }
}

.contacts__wrapper-text {
  max-width: 500px;
  padding-top: 77px;
}

@media  screen and (max-width: 1023px) {
  .contacts__wrapper-text {
    min-width: 354px;
    padding-top: 67px;
    padding-left: 30px;
  }
}

@media  screen and (max-width: 767px) {
  .contacts__wrapper-text {
    padding-top: 0;
  }
}

.contacts__map {
  position: relative;

  padding-right: 100px;

  overflow: hidden;
}


@media  screen and (max-width: 767px) {
  .contacts__map {
    padding-right: 0;
  }
}


.contacts__map iframe {
  position: relative;
  z-index: 2;
  width: 560px;
  height: 306px;

  border: none;
}

@media  screen and (max-width: 1023px) {
  .contacts__map iframe {
    //width: 324px;
    height: 335px;
  }
}

@media  screen and (max-width: 767px) {
  .contacts__map iframe {
    //width: 290px;
    width: 100%;
    height: 316px;
  }
}

.contacts__map img {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;

  margin: 0;
}



