.contacts__map::before {
  content: "";

  position: absolute;
  top: 217px;
  left: 249px;

  width: 18px;
  height: 22px;

  background-image: url("../img/icon-map-pin.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: 18px 22px;
  //background-color: red;
}

@media  screen and (max-width: 1023px) {
  .contacts__map::before {
    top: 217px;
    left: 109px;
  }
}

@media  screen and (max-width: 767px) {
  .contacts__map {
    order: 1;
  }
}


.contacts__map img {
  line-height: 0;
}

.contacts__slogan {
  padding-bottom: 30px;
}

@media  screen and (max-width: 1023px) {
  .contacts__slogan {
    padding-bottom: 26px;
  }
}


.contacts__slogan span {
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  color: #011C40;
}

.contacts__address {
  padding-bottom: 20px;
  padding-left: 28px;
}

.contacts__address span {
  position: relative;

  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  color: #011C40;
}

.contacts__address span::before {
  content: "";

  position: absolute;
  top: -5px;
  left: -28px;

  width: 18px;
  height: 22px;

  background-image: url("../img/icon-map-pin.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: 18px 22px;
  //background-color: red;
}

@media  screen and (max-width: 1023px) {
  .contacts__address span::before {
    top: 2px;
  }
}

.contacts__phone {
  position: relative;

  padding-left: 28px
}

@media  screen and (max-width: 767px) {
  .contacts__phone {
    padding-bottom: 43px;
  }
}

.contacts__phone span::before {
  content: "";

  position: absolute;
  top: 3px;
  left: 0;

  width: 18px;
  height: 18px;

  background-image: url("../img/icon-phone.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: 18px 18px;
  //background-color: red;
}

